import "./FlowerField.css";
export default function FlowerField() {
  return (
    <div className="flowerField">
      <img
        src="/20-10-website/field.png"
        style={{ width: "100%", position: "fixed", bottom: 0, zIndex: 1 }}
      />
      <img
        src="/20-10-website/flower1.png"
        style={{
          position: "fixed",
          zIndex: 10,
          width: "50px",
          bottom: 10,
          right: 20,
        }}
      />
      <img
        src="/20-10-website/flower1.png"
        style={{
          position: "fixed",
          zIndex: 10,
          width: "50px",
          bottom: 20,
          left: 20,
        }}
      />
      <img
        src="/20-10-website/flower2.png"
        style={{
          position: "fixed",
          zIndex: 10,
          width: "80px",
          bottom: 0,
          right: 150,
          transform: "rotate(90deg)",
        }}
      />
      <img
        src="/20-10-website/flower3.png"
        style={{
          position: "fixed",
          zIndex: 10,
          width: "100px",
          bottom: "65%",
          right: -60,
          transform: "rotate(90deg)",
        }}
      />
      <img
        src="/20-10-website/flower4.png"
        style={{
          position: "fixed",
          zIndex: 10,
          width: "100px",
          bottom: "55%",
          left: -30,
          transform: "rotate(90deg)",
        }}
      />
      <img
        src="/20-10-website/flower5.png"
        style={{
          position: "fixed",
          zIndex: 10,
          width: "100px",
          bottom: "45%",
          right: 0,
          transform: "rotate(90deg)",
        }}
      />
    </div>
  );
}
